.textArea-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: inherit;
    margin: 0 auto;
    padding: 10px;
    height: 140px;
}

@media (max-width: 500px) {
    .textArea-container {
        display: flex;
        height: 100px;
    }    
}